<template>
  <div class="wrapper">
    <image :src="logo" class="logo" />
    <text class="greeting">The environment is ready!</text>
    <div @click="jump">
      
    </div>
  </div>
</template>

<script>
import Util from "./config/utils";
const navigator = weex.requireModule("navigator");
const modal = weex.requireModule("modal");
export default {
  name: "App",
  data() {
    return {
      logo: "https://gw.alicdn.com/tfs/TB1yopEdgoQMeJjy1XaXXcSsFXa-640-302.png"
    };
  },
  methods: {
    jump() {
      modal.toast({
        message: Util.setBundleUrl(
          weex.config.bundleUrl,
          "/components/own/Own.js"
        ),
        duration: 4
      });
      navigator.push(
        {
          url: Util.setBundleUrl(
            weex.config.bundleUrl,
            "/components/own/Own.js"
          )
        }
      );
    }
  }
};
</script>

<style scoped>
.wrapper {
  justify-content: center;
  align-items: center;
}
.logo {
  width: 424px;
  height: 200px;
}
.greeting {
  text-align: center;
  margin-top: 70px;
  font-size: 50px;
  color: #41b883;
}
.message {
  margin: 30px;
  font-size: 32px;
  color: #727272;
}
</style>



